import "./App.css";
import { useState } from "react";
import Chat from "./Components/Chatbot/Chatbot";
import SmartHomeAssistant from "./Components/SmartHomeAssistant/SmartHomeDoc";
import ChatbotImage from "./chatbot.png";


function App() {
  const [showChat, setShowChat] = useState(false);

  return (
      <div className="App">
            {showChat ?
                <Chat handleCancel={() => setShowChat(false)}/> :
                (
                    <img
                        src={ChatbotImage}
                        className='floating-image'
                        alt="Floating Icon"
                        onClick={() => setShowChat(!showChat)}
                    />
                )
            }
        <SmartHomeAssistant/>
      </div>
  );
}

export default App;